<script>
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { INITIAL_EVENTS, createEventId } from './components/event-utils'
import ProjectModal from './components/ProjectModal.vue'
import axios from 'axios'

export default {

  components: {
    FullCalendar, // make the <FullCalendar> tag available
    ProjectModal
  },

  data: function() {
    return {
      //axios로 가져올 eventList
      eventList:null,
      key:'',
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        },
        initialView: 'dayGridMonth',
        initialEvents: INITIAL_EVENTS,// alternatively, use the `events` setting to fetch from a feed
         //initialEvents: {
         //  id:createEventId(),
         //  title: eventList[0].project_title,
         //  start: eventList[0].start_date,
         //  end:  eventList[0].due_date
         // }, 
        
        //initialEvents:{
        //  enventList[0]
        //},
        editable: true,
        selectable: true,
        selectMirror: true,
        dayMaxEvents: true,
        weekends: true,
        select: this.handleDateSelect,
        eventClick: this.handleEventClick,
        eventsSet: this.handleEvents,
        /* you can update a remote database when these fire:
        eventAdd:
        eventChange:
        eventRemove:
        */
      },
      currentEvents: [],
      createEvent: this.openProjectModal,
    }
  },

  mounted() {
    console.log(document.getElementsByName('tKey'));
    console.log(document.getElementsByName('tKey')[0].value);
    this.key = document.getElementsByName('tKey')[0].value;
    console.log("key : " + this.key);
    this.fetchData(this.key);
  },

  methods: {
    async fetchData(key){
      try {
        const response = await axios.get("http://localhost:8056/api/project?t_key="+key);
        this.eventList = response.data;
        console.log(this.eventList);
      } catch (error) {
        console.log("axios실패임ㅠ");
        console.log(error);
      }
      

    },


    handleWeekendsToggle() {
      this.calendarOptions.weekends = !this.calendarOptions.weekends // update a property
    },

    handleDateSelect(selectInfo) {
      let title = prompt('일정 제목을 입력하세요')
      let calendarApi = selectInfo.view.calendar

      calendarApi.unselect() // clear date selection

      if (title) {
        calendarApi.addEvent({
          id: createEventId(),
          title,
          start: selectInfo.startStr,
          end: selectInfo.endStr,
          allDay: selectInfo.allDay
        })
      }
    },

    handleEventClick(clickInfo) {
      if (confirm(`'${clickInfo.event.title}' 일정을 삭제하시겠습니까?`)) {
        clickInfo.event.remove()
      }
    },

    handleEvents(events) {
      this.currentEvents = events
    },

    // 모달 열기 메서드 추가
    openProjectModal() {
      this.$refs.projectModal.openModal(); // 모달 열기
      console.log("open Project Modal method 호출")
    }
  },
}
</script>

<template>
  <div class='app'>
    <div class='app-sidebar'>
      <div class='app-sidebar-section'>
        <h2>프로젝트 관리</h2>
      </div>

      <div>
        <!-- {{ eventList[0].project_title }} -->
        {{ eventList }}
      </div>
      
      <div class='app-sidebar-section'>
        <label>
          <input
            type='checkbox'
            :checked='calendarOptions.weekends'
            @change='handleWeekendsToggle'
          />
          주말 포함
        </label>
      </div>
      <div class='app-sidebar-section'>
        <h2>TO DO ({{ currentEvents.length }})</h2>
        <ul>
          <li v-for='event in currentEvents' :key='event.id'>
            <span>{{ event.title }}</span><br>
            <b>{{ event.startStr }}</b>
          </li>
        </ul>
      </div>
    </div>
    <div class='app-main'>
      <FullCalendar
        class='app-calendar'
        :options='calendarOptions'
      >
        <template v-slot:eventContent='arg'>
          <!-- <b>{{ arg.timeText }}</b> -->
          <span>{{ arg.event.title }}</span>
        </template>
      </FullCalendar>
    </div>

    <div>
      <!-- '프로젝트 생성' 버튼 추가 -->
      <button @click="openProjectModal">프로젝트 생성</button>

    </div>
    <!-- 모달 컴포넌트 추가 -->
    <ProjectModal ref="projectModal"></ProjectModal>
  </div>
</template>





